.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-size: 32px;
  line-height: 100%;
  font-weight: 500;
  letter-spacing: -1.5px;
  text-align: center;

  &--first {
    text-align: left;
    margin-bottom: 32px;
  }

  @include onTablet {
    font-size: 48px;

    &--first {
      margin-bottom: 0;
    }
  }

  @include onDesktop {
    margin-bottom: 56px;
    font-size: 64px;

    &--font-size {
      font-size: 48px;
    }

    &--first {
      margin-bottom: 0;
    }
  }
}
